
html, body, div, ul, li, h1, h2, h3, h4, h5, h6, p, dl, dt, dd, ol, form, input, textarea, th, td, select {
  margin: 0;
  padding: 0;
}
*{-webkit-box-sizing: border-box;box-sizing: border-box;
}
html, body {
  min-height: 100%;
}
body {
  font-family: "Microsoft YaHei";
  font-size:14px;
  color:#333;
  background-size: 100% 100%;
  background-repeat:no-repeat;
  background-attachment: fixed;
}
h1, h2, h3, h4, h5, h6{font-weight:normal;
}
ul,ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
a {
  text-decoration: none;
  color: #232323;
}
table {
  border-collapse: collapse;
  table-layout: fixed;
}
#app {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
}
#particlesId{
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}
#particlesId .particles-js-canvas-el{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.herds { width: 100%;height: 90px;background: rgba(255, 255, 255, 0.52);border-bottom-left-radius: 20px;border-bottom-right-radius: 20px;font-size: 14px;-webkit-box-shadow: 0 0 25px rgba(74, 59, 114,1);box-shadow: 0 0 25px rgba(74, 59, 114,1);
}
.herds-nav {position: fixed;width:100%;top: -90px;-webkit-transition: top .5s;transition: top .5s;
}
.herds-nav-appear {top: 0;
}
.herdsNav {
    margin: 0 auto;
    width: 1200px;
    height: 62px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.52);
}
@media screen and (max-width: 1200px) {
.herdsNav {
    width: 100% !important;
}
}
.herdsNav img {
    float: left;
    width: 62px;
    height: 62px;
    border-radius: 50%;
}
.Amaya_liu {
    float: left;
    margin-left: 15px;
    height: 62px;
    line-height: 62px;
}
.NavRight {
    float: right;
}
@media (max-width: 991px) and (min-width: 750px) {
.NavRight {
      margin-right: 20px;
}
}
.buttonBox {
    position: relative;
    float: left;
    margin: 0 15px;
    line-height: 62px;
}
.NavRight button {
    width: 100px;
    height: 37px;
    position: relative;
    background: rgba(74, 59, 114,0.8);
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    border: none;
    font-size: 14px;
    outline: none;
    font-family: "\534E\6587\6977\4F53";
}
.border {
    position: absolute;
    background: none;
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}
.all {
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}
.buttonBox:hover .border{
    width: 102px !important;
    height: 37px !important;
}
#first > .border:nth-of-type(1) {
    top: 12px;
    left: -1px;
    border-left: 1px solid white;
    border-top: 1px solid white;
    width: 20px;
    height: 20px;
}
#first > .border:nth-of-type(2) {
    bottom: 11px;
    right: -1px;
    border-right: 1px solid white;
    border-bottom: 1px solid white;
    width: 20px;
    height: 20px;
}
